var getRmb=require('../../common/requset.js')
export default {
    data(){
        return {
            instant:'',
            type:'买入',
            state:'',
            cander:'',
            price:'',
            hberd:'',
            rmb:'',
            frte:true,
            checkall:'展示完整信息'
        }
    },
    onLoad(option) {
        var that = this
        //获取上个页面携带的数据，并复制
        var cander = JSON.parse(option.dospp)
        that.cander = cander
        var hberd = that.cander[1]
        that.hberd = that.cander[1]
        that.state = cander[0][0].order_status
        if(that.state == 103) {
            that.state = '待审核'
        }
        
        that.instant = that.cander[0][0]
        that.price = that.instant.sale_money
        that.rmb = getRmb.getrmb(that.price)
    },
    methods:{
        btn:function () {
            var that = this
            that.frte=!that.frte;
            if(that.frte==false){
                that.checkall='收起';
            }else{
                that.checkall='展示完整信息'
            }
        }
    }
}
